import axios_config from "../config";

const setCategoria = async (categoria) => {
  let response = await axios_config.post("categoria", categoria);
  return response;
};

/**
 *
 * @param {Object} categoria
 * @returns {Object}
 */
const setUpdateCategoria = async (categoria) => {
  let response = await axios_config.put(`categoria/${categoria.id}`, categoria);
  return response;
};

export default {
  setCategoria,
  setUpdateCategoria,
};
