<template>
  <div>
    <v-form @submit.prevent="onSend">
      <v-container>
        <v-row>
          <v-col cols="12" class="mt-5">
            <p class="text-h4">
              <v-icon size="38" color="black">mdi-archive-outline</v-icon>
              PRESENTACIONES
            </p>
            <v-btn
              color="success"
              class="text-button"
              v-show="!config.isVisibleForm"
              @click="config.isVisibleForm = !config.isVisibleForm"
              >AGREGAR</v-btn
            >
            <v-btn
              color="warning"
              class="text-button"
              v-show="config.isVisibleForm"
              @click="onHideForm()"
              >Esconder</v-btn
            >
          </v-col>
        </v-row>
        <v-row v-show="config.isVisibleForm">
          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="text"
              v-model="presentacion.nombre"
              label="Presentación"
              color="red lighten-3"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row v-show="config.isVisibleForm">
          <v-col cols="12">
            <v-btn color="success" type="submit" class="text-button">
              <v-icon>mdi-content-save</v-icon>
              Guardar
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>

    <v-spacer></v-spacer>

    <v-container>
      <v-simple-table fixed-header height="400px">
        <template v-slot:default>
          <thead>
            <tr>
              <th>#</th>
              <th>Presentación</th>
              <th>Opciones</th>
            </tr>
          </thead>

          <tbody>
            <tr
              v-for="(presentacion, index) in presentaciones.obj"
              :key="presentacion.id"
            >
              <td>{{ index + 1 }}</td>
              <td>{{ presentacion.nombre }}</td>
              <td>
                <v-btn
                  color="info"
                  title="Editar"
                  small
                  @click="setPresentacionUpdate(presentacion)"
                >
                  <v-icon small>mdi-pencil</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </div>
</template>

<script>
/* Service */
import Service from "./../../service/index.js";
import PresentacionService from "./../../service/presentacion/index.js";

export default {
  name: "Presentacion",
  async mounted() {
    this.getAllPresentaciones();
  },
  data() {
    return {
      presentaciones: [],
      presentacion: {
        id: 0,
        nombre: "",
      },
      config: {
        isVisibleForm: false,
      },
    };
  },
  methods: {
    onSend() {
      if (this.presentacion.id === 0) {
        this.onSave();
      } else {
        this.onUpdate();
      }
    },

    async onSave() {
      let response = await PresentacionService.setPresentacion(this.presentacion);
      this.onHideForm();
      this.getAllPresentaciones();
    },

    async onUpdate() {
      let response = await PresentacionService.setUpdatePresentacion(this.presentacion);
      this.getAllPresentaciones();
      this.onHideForm();
    },

    onHideForm() {
      this.config.isVisibleForm = false;
      this.clear();
    },

    /* Funciones de ayuda para la vista */
    setPresentacionUpdate(presentacion) {
      this.presentacion = { ...presentacion };
      console.log(this.presentacion);
      this.config.isVisibleForm = true;
    },

    async getAllPresentaciones() {
      this.presentaciones = await Service.getAll("presentacion");
    },

    clear() {
      this.presentacion = {
        id: 0,
        nombre: "",
      };
    },
  },
};
</script>
