import axios_config from "../config";

/**
 * Función para guardar un nuevo producto
 * @param {Object} producto contiene el producto que se va a guardar en la bd
 * @returns {Promise}
 */
const setProducto = async (producto) => {
  let response = await axios_config.post("producto", producto);
  return response;
};

/**
 * Función para actualizar datos de un producto
 * @param {Object} producto
 */
const setUpdateProducto = async (producto) => {
  let response = await axios_config.put(`producto/${producto.id}`, producto);
  return response;
};

/**
 * Función recuperar las presentaciones
 * @returns {Promise} lista de presentaciones
 */
const getPresentaciones = async () => {
  let presentaciones = await axios_config.get("presentacion");
  return presentaciones.data.obj;
};

/**
 * Función para recuperar la lista de categorias
 * @returns {Promise} lista de categorias
 */
const getCategorias = async () => {
  let categorias = await axios_config.get("categoria");
  return categorias.data.obj;
};

export default {
  setProducto,
  setUpdateProducto,
  getPresentaciones,
  getCategorias,
};
