import Vue from "vue";
import VueRouter from "vue-router";
import Index from "../views/Index.vue";
import Login from "../views/Login.vue";
import Home from "../views/Home.vue";
import Producto from "./../components/producto/Producto.vue";
import Persona from "./../components/persona/Persona.vue";
import Presentacion from "./../components/presentacion/Presentacion.vue";
import Categoria from "./../components/categoria/Categoria.vue";
import Socio from "./../components/socio/Socio.vue";
import Cliente from "./../components/cliente/Cliente.vue";

Vue.use(VueRouter);

// route level code-splitting
// this generates a separate chunk (about.[hash].js) for this route
// which is lazy-loaded when the route is visited.
const routes = [
  {
    path: "/",
    name: "index",
    component: Index,
  },
  {
    path: "/login",
    name: "login",
    component: Login,
  },
  {
    path: "/home",
    name: "home",
    component: Home,
  },
  {
    path: "/producto",
    name: "producto",
    component: Producto,
  },
  {
    path: "/persona",
    name: "persona",
    component: Persona,
  },

  {
    path: "/cliente",
    name: "cliente",
    component: Cliente,
  },
  {
    path: "/socio",
    name: "socio",
    component: Socio,
  },
  {
    path: "/presentacion",
    name: "presentacion",
    component: Presentacion,
  },
  {
    path: "/categoria",
    name: "categoria",
    component: Categoria,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
