<template>
  <div>
    <v-form @submit.prevent="onSend">
      <v-container>
        <v-row>
          <v-col cols="12" class="mt-5">
            <p class="text-h4">
              <v-icon size="38" color="black">mdi-shape</v-icon>
              CATEGORIA
            </p>
            <v-btn
              color="success"
              class="text-button"
              v-show="!config.isVisibleForm"
              @click="config.isVisibleForm = !config.isVisibleForm"
              >AGREGAR</v-btn
            >
            <v-btn
              color="warning"
              class="text-button"
              v-show="config.isVisibleForm"
              @click="onHideForm()"
              >Esconder</v-btn
            >
          </v-col>
        </v-row>
        <v-row v-show="config.isVisibleForm">
          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="text"
              v-model="categoria.nombre"
              label="Categoria"
              color="red lighten-3"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row v-show="config.isVisibleForm">
          <v-col cols="12">
            <v-btn color="success" type="submit" class="text-button">
              <v-icon>mdi-content-save</v-icon>
              Guardar
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>

    <v-spacer></v-spacer>

    <v-container>
      <v-simple-table fixed-header height="400px">
        <template v-slot:default>
          <thead>
            <tr>
              <th>#</th>
              <th>Categoria</th>
              <th>Opciones</th>
            </tr>
          </thead>

          <tbody>
            <tr
              v-for="(categoria, index) in categorias.obj"
              :key="categoria.id"
            >
              <td>{{ index + 1 }}</td>
              <td>{{ categoria.nombre }}</td>
              <td>
                <v-btn
                  color="info"
                  title="Editar"
                  small
                  @click="setCategoriaUpdate(categoria)"
                >
                  <v-icon small>mdi-pencil</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </div>
</template>

<script>
/* Service */
import Service from "./../../service/index.js";
import CategoriaService from "./../../service/categoria/index.js";

export default {
  name: "Categoria",
  async mounted() {
    this.getAllCategorias();
  },
  data() {
    return {
      categorias: [],
      categoria: {
        id: 0,
        nombre: "",
      },
      config: {
        isVisibleForm: false,
      },
    };
  },
  methods: {
    onSend() {
      if (this.categoria.id === 0) {
        this.onSave();
      } else {
        this.onUpdate();
      }
    },

    async onSave() {
      let response = await CategoriaService.setCategoria(
        this.categoria
      );
      this.onHideForm();
      this.getAllCategorias();
    },

    async onUpdate() {
      let response = await CategoriaService.setUpdateCategoria(
        this.categoria
      );
      this.getAllCategorias();
      this.onHideForm();
    },

    onHideForm() {
      this.config.isVisibleForm = false;
      this.clear();
    },

    /* Funciones de ayuda para la vista */
    setCategoriaUpdate(categoria) {
      this.categoria = { ...categoria };
      this.config.isVisibleForm = true;
    },

    async getAllCategorias() {
      this.categorias = await Service.getAll("categoria");
    },

    clear() {
      this.categoria = {
        id: 0,
        nombre: "",
      };
    },
  },
};
</script>
