import axios_config from "../config";

/**
 * Función para guardar una nueva presentacion
 * @param {Object} presentacion
 * @returns {Promise}
 */
const setPresentacion = async (presentacion) => {
  let response = await axios_config.post("presentacion", presentacion);
  return response;
};

/**
 *
 * @param {Object} presentacion
 * @returns {Promise}
 */
const setUpdatePresentacion = async (presentacion) => {
  let response = await axios_config.put(
    `presentacion/${presentacion.id}`,
    presentacion
  );
  return response;
};

export default {
  setPresentacion,
  setUpdatePresentacion,
};
