<template>
  <div>
    <v-form @submit.prevent="onSend">
      <v-container>
        <v-row>
          <v-col cols="12" class="mt-5">
            <p class="text-h4">
              <v-icon size="48" color="black">mdi-account-group</v-icon>
              CLIENTES
            </p>
            <v-btn
              color="success"
              class="text-button"
              v-show="!config.isVisibleForm"
              @click="config.isVisibleForm = !config.isVisibleForm"
              >AGREGAR</v-btn
            >
            <v-btn
              color="warning"
              class="text-button"
              v-show="config.isVisibleForm"
              @click="onHideForm()"
              >Esconder</v-btn
            >
          </v-col>
        </v-row>
        <v-row v-show="config.isVisibleForm">
          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              v-model="persona.nombre"
              label="Nombre"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              v-model="persona.apellido_paterno"
              label="Apellido paterno"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="text"
              v-model="persona.apellido_materno"
              label="Apellido materno"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="text"
              v-model="persona.dni"
              label="DNI"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="text"
              v-model="persona.celular"
              label="Celular"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="text"
              v-model="persona.direccion"
              label="Dirección"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-select
              :items="[
                { id: 'F', nombre: 'Femenino' },
                { id: 'M', nombre: 'Masculino' },
              ]"
              v-model="persona.genero"
              item-value="id"
              item-text="nombre"
              label="Genero"
              color="red lighten-3"
            >
            </v-select>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="text"
              v-model="persona.correo"
              label="Correo"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="password"
              v-model="persona.password"
              label="Contraseña"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="password"
              v-model="persona.password_confirmation"
              label="Confirma contraseña"
              color="red lighten-3"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row v-show="config.isVisibleForm">
          <v-col cols="12">
            <v-btn color="warning" type="reset" class="text-button mr-3">
              <v-icon>mdi-content-save</v-icon>
              Borrar
            </v-btn>
            <v-btn color="success" type="submit" class="text-button">
              <v-icon>mdi-content-save</v-icon>
              Guardar
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>

    <v-spacer></v-spacer>

    <v-container>
      <v-simple-table fixed-header height="400px">
        <template v-slot:default>
          <thead>
            <tr>
              <th>#</th>
              <th>Nombre completo</th>
              <th>DNI</th>
              <th>Celular</th>
              <th>Correo</th>
              <th>Dirección</th>
              <th>Genero</th>
              <th>Opciones</th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="(persona, index) in personas.obj" :key="persona.id">
              <td>{{ index + 1 }}</td>
              <td>{{ persona.nombre_completo }}</td>
              <td>{{ persona.dni }}</td>
              <td>{{ persona.celular }}</td>
              <td>{{ persona.correo }}</td>
              <td>{{ persona.direccion }}</td>
              <td>{{ persona.nombre_genero }}</td>
              <td>
                <v-btn
                  color="info"
                  title="Editar"
                  @click="setPersonaUpdate(persona)"
                  small
                >
                  <v-icon small>mdi-pencil</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </div>
</template>

<script>
/* Service */
import PersonaService from "./../../service/persona/index.js";

export default {
  name: "Cliente",
  async mounted() {
    await this.getAllPersonas();
  },
  data() {
    return {
      personas: [],
      persona: {
        id: 0,
        nombre: "",
        apellido_paterno: "",
        apellido_materno: "",
        dni: "",
        celular: "",
        correo: "",
        password: "",
        password_confirmation: "",
        genero: "F",
        tipo_persona: 1,
        direccion: "",
      },
      config: {
        isVisibleForm: true,
      },
    };
  },
  methods: {
    onSend() {
      if (this.persona.id === 0) {
        this.onSave();
      } else {
        this.onUpdate();
      }
    },

    async onSave() {
      let response = await PersonaService.setPersona(this.persona);
      console.log(response);
      this.onHideForm();
      this.getAllPersonas();
    },

    async onUpdate() {
      let response = await PersonaService.setUpdatePersona(this.persona);
      this.getAllPersonas();
      this.onHideForm();
    },

    onHideForm() {
      this.config.isVisibleForm = false;
      this.clear();
    },

    /* Funciones de ayuda para la vista */

    setPersonaUpdate(persona) {
      this.persona = { ...persona };
      this.config.isVisibleForm = true;
    },

    async getAllPersonas() {
      this.personas = await PersonaService.getPersona("1");
    },

    clear() {
      this.persona = {
        id: 0,
        nombre: "",
        apellido_paterno: "",
        apellido_materno: "",
        dni: "",
        celular: "",
        correo: "",
        genero: "F",
        tipo_persona: 1,
        direccion: "",
      };
    },
  },
};
</script>
