import axios_config from "../config";

const getPersona = async (param) => {
  let data = await axios_config.get(`persona/${param}`);
  return data.data;
};

const setPersona = async (persona) => {
  let response = await axios_config.post("persona", persona);
  return response;
};

const setUpdatePersona = async (persona) => {
  let response = await axios_config.put(`persona/${persona.id}`, persona);
  return response;
};

export default {
  getPersona,
  setPersona,
  setUpdatePersona,
};
