<template>
  <div>
    <v-form @submit.prevent="onSend">
      <v-container>
        <v-row>
          <v-col cols="12" class="mt-5">
            <p class="text-h4">
              <v-icon size="38" color="black">mdi-archive-outline</v-icon>
              PRODUCTOS
            </p>
            <v-btn
              color="success"
              class="text-button"
              v-show="!config.isVisibleForm"
              @click="config.isVisibleForm = !config.isVisibleForm"
              >AGREGAR</v-btn
            >
            <v-btn
              color="warning"
              class="text-button"
              v-show="config.isVisibleForm"
              @click="onHideForm()"
              >Esconder</v-btn
            >
          </v-col>
        </v-row>
        <v-row v-show="config.isVisibleForm">
          <v-col cols="12" sm="6" md="4" xl="3">
            <v-select
              :items="presentaciones"
              v-model="producto.id_presentacion"
              item-value="id"
              item-text="nombre"
              label="Presentación"
              color="red lighten-3"
              item-color="red lighten-3"
            ></v-select>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-select
              :items="categorias"
              v-model="producto.id_categoria"
              item-value="id"
              item-text="nombre"
              label="Categoria"
              color="red lighten-3"
            >
            </v-select>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              v-model="producto.codigo"
              label="Código"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              v-model="producto.nombre"
              label="Producto"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="number"
              v-model="producto.precio"
              label="Precio del producto"
              color="red lighten-3"
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="6" md="4" xl="3">
            <v-text-field
              type="number"
              v-model="producto.precio_compra"
              label="Precio compra del producto"
              color="red lighten-3"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row v-show="config.isVisibleForm">
          <v-col cols="12">
            <v-btn color="success" type="submit" class="text-button">
              <v-icon>mdi-content-save</v-icon>
              Guardar
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>

    <v-spacer></v-spacer>

    <v-container>
      <v-simple-table fixed-header height="400px">
        <template v-slot:default>
          <thead>
            <tr>
              <th>#</th>
              <th>Producto</th>
              <th>Precio compra</th>
              <th>Precio venta</th>
              <th>Stock</th>
              <th>Opciones</th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="(producto, index) in productos.obj" :key="producto.id">
              <td>{{ index + 1 }}</td>
              <td>{{ producto.nombre }}</td>
              <td>{{ producto.precio_compra }}</td>
              <td>{{ producto.precio }}</td>
              <td>{{ producto.stock }}</td>
              <td>
                <v-btn
                  color="info"
                  title="Editar"
                  small
                  @click="setProductoUpdate(producto)"
                >
                  <v-icon small>mdi-pencil</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </div>
</template>

<script>
/* Service */
import ProductoService from "./../../service/producto/index.js";
import Service from "./../../service/index.js";

export default {
  name: "Producto",
  async mounted() {
    //trayendo los datos necesarios
    this.presentaciones = await ProductoService.getPresentaciones();
    this.categorias = await ProductoService.getCategorias();
    this.getAllProductos();
  },
  data() {
    return {
      presentaciones: [],
      categorias: [],
      productos: [],
      producto: {
        id: 0,
        id_presentacion: null,
        id_categoria: null,
        codigo: "#",
        nombre: null,
        precio: null,
        precio_compra: null,
      },
      config: {
        isVisibleForm: false,
      },
    };
  },
  methods: {
    onSend() {
      if (this.producto.id === 0) {
        this.onSave();
      } else {
        this.onUpdate();
      }
    },

    async onSave() {
      let response = await ProductoService.setProducto(this.producto);
      console.log(response);
      this.onHideForm();
      this.getAllProductos();
    },

    async onUpdate() {
      let response = await ProductoService.setUpdateProducto(this.producto);
      this.getAllProductos();
      this.onHideForm();
    },

    onHideForm() {
      this.config.isVisibleForm = false;
      this.clear();
    },

    /* Funciones de ayuda para la vista */

    setProductoUpdate(producto) {
      this.producto = { ...producto };
      this.config.isVisibleForm = true;
    },

    async getAllProductos() {
      this.productos = await Service.getAll("producto");
    },

    clear() {
      this.producto = {
        id: 0,
        id_presentacion: null,
        id_categoria: null,
        codigo: "#",
        producto: null,
        precio: null,
        precio_compra: null,
      };
    },
  },
};
</script>
