      },
    };
  },
  methods: {
    onSend() {
      if (this.persona.id === 0) {
        this.onSave();
      } else {
        this.onUpdate();
      }
    },

    async onSave() {
      let response = await ProductoService.setProducto(this.producto);
      console.log(response);
      this.onHideForm();
      this.getAllProductos();
    },

    async onUpdate() {
      let response = await ProductoService.setUpdateProducto(this.producto);
      this.getAllProductos();
      this.onHideForm();
    },

    onHideForm() {
      this.config.isVisibleForm = false;
      this.clear();
    },

    /* Funciones de ayuda para la vista */

    setProductoUpdate(producto) {
      this.producto = { ...producto };
      this.config.isVisibleForm = true;
    },

    async getAllProductos() {
      this.productos = await Service.getAll("producto");
    },

    clear() {
      this.producto = {
        id: 0,
        id_presentacion: null,
        id_categoria: null,
        codigo: "#",
        producto: null,
        precio: null,
        precio_compra: null,
      };
    },
  },
};
